html,
body {
	height: 100%;
}

table {
	width: 800px;
	border-collapse: collapse;
	overflow: hidden;
}

.container {
	position: relative;
	margin-top: 24px;
}

.leaderboard {
	overflow-y: scroll;
}

tr:hover:not(:first-child) {	
	background-color: rgba(255,255,255,0.3);
    transition-duration: 0.15s;
}


th, td {
    text-align: left;
	padding: 15px;
}